$cell-size: 100px;
$cell-border: 1px;
$border-color: #000;
$activ-color:#fbc;
$cell-color: #eee;

$count-days: 31;
$count-week: 7;

$gap: 1px;

$calendar-area: $count-week * ($cell-size + $gap + 2 * $cell-border);

$days: 
  'mon' 0,
  'tue' 1,
  'wed' 2,
  'thu' 3,
  'fri' 4,
  'sat' 5,
  'sun' 6;

$mounth:
  '29' 29,
  '30' 30,
  '31' 31;