.calendar {
    display: flex;
    flex-wrap: wrap;
    gap: $gap;
    margin: 0 auto;
    max-width: #{$calendar-area};
  
    &__day {
      display: flex;
      align-items: center;
      justify-content: center;
      @include square($cell-size);
      background-color: $cell-color;
      border: $cell-border solid #{$border-color};
  
  
      &:hover {
        background-color: $activ-color;
        cursor: pointer;
        transform: translateY(-20px);
        transition: transform 500ms;
      }
    }
  
    @for $index from 1 through $count-days {
      &__day:nth-child(#{$index})::before {
        content: "#{$index}";
        display: flex;
        align-self: center;
        @extend %large-text;
      }
    }
  
    @each $name, $value in $days {
      &__start-day--#{$name} :first-child {
        margin-left: $value * ($cell-size + $gap);
      }
    }
  
    @for $index from 28 through $count-days {
      &__month-length--#{$index}
      &__day:nth-child(n + #{$index + 1}) {
        display: none;
      }
    }
  }